<template>
  <div id="data-form-container" class="data-form-container" ref="container">
    <div class="nick">
      <input type="text" maxlength="10" placeholder="用户昵称" v-model="formData.nickname" />
      <span>{{ formData.nickname.length }}/10</span>
    </div>
    <p class="error-message">{{ error.nickname }}</p>
    <div class="content">
      <textarea cols="30" rows="10" maxlength="300" placeholder="输入内容" v-model="formData.content"></textarea>
      <span>{{ formData.content.length }}/300</span>
    </div>
    <p class="error-message">{{ error.content }}</p>
    <button
      class="btn"
      @click="handleSubmit"
      :disabled="isSubmiting"
    >{{ isSubmiting ? "提交中..." : "提交" }}</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formData: {
        nickname: "",
        content: "",
      },
      error: {
        nickname: "",
        content: "",
      },
      isSubmiting: false,
    };
  },
  methods: {
    handleSubmit() {
      this.error.nickname = this.formData.nickname ? "" : "请填写名称";
      this.error.content = this.formData.content ? "" : "请填写内容";
      if (this.error.nickname || this.error.content) {
        return;
      }
      this.isSubmiting = true;
      this.$emit("submit", this.formData, (msg) => {
        this.formData.nickname = "";
        this.formData.content = "";
        this.isSubmiting = false;
        this.$showMessage({
          content: msg,
          type: "success",
          container : this.$refs.container
        });
      });
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.data-form-container {
  font-size: 12px;
  color: @gray;
  .nick {
    position: relative;
    input {
      width: 400px;
      border: 1px dashed @gray;
      height: 40px;
      border-radius: 5px;
      padding-left: 10px;
      letter-spacing: 1px;
      font-size: 12px;
      &:focus {
        border: 1px dashed @primary;
        outline: none;
      }
    }
    span {
      position: relative;
      right: 50px;
      text-align: right;
      display: inline-block;
      width: 40px;
    }
  }
  .content {
    position: relative;
    textarea {
      border: 1px dashed @gray;
      border-radius: 5px;
      padding-left: 10px;
      padding-top: 10px;
      width: 500px;
      resize: none;
      font-size: 12px;
      &:focus {
        border: 1px dashed @primary;
        outline: none;
      }
    }
    span {
      position: relative;
      right: 70px;
      bottom: 10px;
      width: 60px;
      display: inline-block;
      text-align: right;
    }
  }
  .error-message {
    color: @warn;
    height: 28px;
    margin: 0;
    line-height: 28px;
    font-size: 14px;
  }
  .btn {
    background-color: @primary;
    border: none;
    color: #fff;
    padding: 8px 30px;
    border-radius: 5px;
    cursor: pointer;
    &:hover {
      background-color: darken(@primary, 10%);
    }
    &:disabled {
      background-color: lighten(@primary, 20%);
      cursor: not-allowed;
    }
  }
}
</style>