<style lang="less" scoped>
@import "~@/styles/var.less";
.contact-container {
  margin: 0;
  padding: 20px;
  list-style: none;
  color: @gray;
  li {
    position: relative;
    height: 30px;
    line-height: 30px;
    margin: 14px 0;
    cursor: pointer;
    &:hover .pop{
      transform: scaleY(1);
    }
    a {
      display: flex;
      align-items: center;
      .icon {
        width: 36px;
        font-size: 26px;
      }
    }
    .pop{
      transition: .5s;
      position: absolute;
      bottom : 30px;
      left: 0;
      padding: 10px 15px;
      background-color: #fff;
      border-radius: 10px;
      transform: scaleY(0);
      transform-origin: bottom;
      img{
        width: 150px;
      }
      &::after{
        content: "";
        display: block;
        position: absolute;
        left: 50%;
        bottom: -4px;
        transform: translateX(-50%) rotate(45deg);
        transform-origin: center bottom;
        width: 8px;
        height: 8px;
        background-color: #fff;

      }
    }
  }
}
</style>