<template>
  <i class="iconfont icon-container" :class="fontClass"></i>
</template>

<script>
const classMap = {
  home: "iconHomehomepagemenu",
  empty : "iconempty",
  iconEmail : "iconyouxiangtubiao",
  iconQq : "iconqq",
  iconWx : "iconweixin1",
  blog : "iconwenzhang",
  about : "iconinfo",
  project : "iconxiangmucaidan",
  message : "iconxiaoxi",
  success : "iconsuccess",
  warn : "iconwarning",
  error : "iconerror",
  info : "iconinfo",
  arrowUp : "iconxiangshang",
  arrowDown : "iconxiangxia"
};
export default {
  props: {
    type: {
      type: String
    }
  },
  computed: {
      fontClass(){
          return classMap[this.type];
      }
  }
};
</script>

<style>
@import "//at.alicdn.com/t/font_2489737_99k5okyj90v.css";
.icon-container{
  font-size: inherit;
}
</style>