<template>
  <div class="home-container" v-loading="loading" ref="container" @wheel="handleWheel">
    <ul class="carousel-container" :style="{marginTop : marginTop}" @transitionend="handleTransform">
      <li v-for="item in data" :key="item.id">
        <Carouselitem
          :bigImg="item.bigImg"
          :midImg="item.midImg"
          :title="item.title"
          :description="item.description"
        />
      </li>
    </ul>
    <div v-show="index > 0" class="icon icon-up" @click="changeTo(index-1)">
      <Icon type="arrowUp" />
    </div>
    <div v-show="index < data.length - 1" class="icon icon-down" @click="changeTo(index+1)">
      <Icon type="arrowDown" />
    </div>
    <ul class="indicator">
      <li
        v-for="(item,i) in data"
        :key="item.id"
        :class="{active:i === index}"
        @click="changeTo(i)"
      ></li>
    </ul>
  </div>
</template>

<script>
import Carouselitem from "./Carouselitem.vue";
import Icon from "@/components/Icon";
import { mapState } from "vuex";
export default {
  components: {
    Icon,
    Carouselitem,
  },
  data() {
    return {
      index: 0, //  当前为第几页
      containerHeight: 0, // 整个容器的高度
      wheeling : false,   // 滚动中
    };
  },
  mounted() {
    this.containerHeight = this.$refs.container.clientHeight;
    window.addEventListener("resize",this.handleResize);
  },
  created() {
    this.$store.dispatch("banner/fetchBanner")
  },
  computed: {
    marginTop() {
      return -this.index * this.containerHeight + "px";
    },
    ...mapState("banner",["loading","data"]),
  },
  destroyed(){
    window.removeEventListener("resize",this.handleResize);
  },
  methods: {
    changeTo(i) {
      this.index = i;
    },
    handleWheel(e) {
      if(this.wheeling){
        return;
      }
      if (e.deltaY > 100 && this.index < this.data.length - 1) {
        // 向下滚动
        this.index ++;
        this.wheeling = true;
      } else if (e.deltaY < -100 && this.index > 0) {
        // 向上滚动
        this.index --;
        this.wheeling = true;
      }
    },
    handleTransform(){
      this.wheeling = false;
    },
    handleResize(){
      this.containerHeight = this.$refs.container.clientHeight;
    }
  },
};
</script>

<style lang="less" scoped>
@import "~@/styles/var.less";
.home-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  .carousel-container {
    width: 100%;
    height: 100%;
    li {
      width: 100%;
      height: 100%;
    }
  }
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    transition: 0.5s;
  }
  @dis: 15px;
  .icon {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-size: 25px;
    cursor: pointer;
  }
  .icon-up {
    top: @dis;
    animation: jump-up 2s infinite;
  }
  .icon-down {
    bottom: @dis;
    animation: jump-down 2s infinite;
  }
  @offset: 5px;
  @keyframes jump-up {
    0% {
      transform: translateY(-@offset);
    }
    50% {
      transform: translateY(@offset);
    }
    100% {
      transform: translateY(-@offset);
    }
  }

  @keyframes jump-down {
    0% {
      transform: translateY(@offset);
    }
    50% {
      transform: translateY(-@offset);
    }
    100% {
      transform: translateY(@offset);
    }
  }
  .indicator {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: @dis;
    li {
      width: 5px;
      height: 5px;
      border-radius: 50%;
      margin-bottom: 5px;
      background-color: @words;
      border: 2px solid #fff;
      cursor: pointer;
      &.active {
        background-color: #fff;
      }
    }
  }
}
</style>