<template>
  <div class="pager-container" v-if="totalPage > 1">
    <a @click="handleClick(1)" :class="{disabled:current === 1}">|&lt;&lt;</a>
    <a @click="handleClick(current - 1)" :class="{disabled:current === 1}">&lt;&lt;</a>
    <a
      @click="handleClick(page)"
      v-for="(page,index) in pageNumber"
      :key="index"
      :class="{active:current === page}"
    >{{page}}</a>
    <a @click="handleClick(current + 1)" :class="{disabled:current === totalPage}">&gt;&gt;</a>
    <a @click="handleClick(totalPage)" :class="{disabled:current === totalPage}">&gt;&gt;|</a>
  </div>
</template>

<script>
export default {
  props: {
    current: {
      // 当前页码
      type: Number,
      default: 1,
    },
    total: {
      // 总数据量
      type: Number,
      default: 0,
    },
    limit: {
      // 页容量
      type: Number,
      default: 10,
    },
    visibleNumber: {
      // 可见页码数
      type: Number,
      default: 10,
    },
  },
  computed: {
    totalPage() {
      return Math.ceil(this.total / this.limit);
    },
    pageNumber() {
      let arr = [];
      for (let i = this.visibleMin; i <= this.visibleMax; i++) {
        arr.push(i);
      }
      return arr;
    },
    visibleMin() {
      let min = this.current - Math.ceil(this.visibleNumber / 2);
      if (min < 1) {
        min = 1;
      }
      return min;
    },
    visibleMax() {
      let max = this.visibleMin + this.visibleNumber - 1;
      if (max > this.totalPage) {
        max = this.totalPage;
      }
      return max;
    },
  },
  methods: {
    handleClick(newPage) {
      if (newPage > this.totalPage) {
        newPage = this.totalPage;
      }
      if (newPage < 1) {
        newPage = 1;
      }
      if (newPage === this.current) {
        return;
      }
      this.$emit("pageChange", newPage);
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/styles/var.less";
.pager-container {
  display: flex;
  justify-content: center;
  margin: 20px 0;
  a {
    color: @primary;
    margin: 0 6px;
    cursor: pointer;
    &.disabled {
      color: @lightWords;
      cursor: not-allowed;
    }
    &.active {
      color: @words;
      font-weight: bold;
      cursor: text;
    }
  }
}
</style>