<template>
  <div class="image-loader-container">
    <img class="placeholder" :src="placeholder" v-if="!everythingOk" />
    <img @load="handleLoad" :src="src" :style="{opacity : originOpacity, transition : `${duration}ms`}"  />
  </div>
</template>

<script>
export default {
  props: {
    src: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      required: true,
    },
    duration: {
      type: Number,
      default: 500,
    },
  },
  data() {
    return {
      imageLoaded: false, // 图片是否加载完成
      everythingOk: false, // 所有事情结束
    };
  },
  computed: {
    originOpacity() {
      return this.imageLoaded ? 1 : 0;
    },
  },
  methods: {
    handleLoad() {
      this.imageLoaded = true;
      setTimeout(() => {
          this.everythingOk = true;
          this.$emit("load");
      }, this.duration);
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/styles/mixin.less";
.image-loader-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  img {
    .self-fill();
  }
  .placeholder{
      filter: blur(20px);
  }
}
</style>