<template>
  <div class="project-container" ref="projectContainer" v-loading="loading">
    <div class="project-item" v-for="item in data" :key="item.id">
      <a :href="item.url?item.url : 'javascript:void(0)'" :target="item.url ? '_blank' : '_self'">
        <img class="thumb" v-lazy="item.thumb" alt />
      </a>
      <div class="info">
        <a :href="item.url?item.url : 'javascript:void(0)'" :target="item.url ? '_blank' : '_self'">
          <h2>{{item.name}}</h2>
        </a>
        <a v-if="item.github" class="github" :href="item.github" target="_blank">Github</a>
        <p v-for="(d,i) in item.description" :class="{technology:i==0}" :key="i">{{d}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import mainScroll from "@/mixins/mainScroll.js";
export default {
  mixins: [mainScroll("projectContainer")],
  computed: mapState("project", ["data", "loading"]),
  created() {
    this.$store.dispatch("project/fetchProject");
  }
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.project-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow-y: auto;
  padding: 10px;
  box-sizing: border-box;
  scroll-behavior: smooth;
  .project-item {
    display: flex;
    padding: 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #b4b8bc;
    transition: all .2s;
    &:hover {
      box-shadow: 1px 1px 5px 0px #000;
    }
    .thumb {
      width: 200px;
      height: 150px;
    }
    .info {
      padding-left: 20px;
      box-sizing: border-box;
      h2 {
        display: block;
        font-size: 1.17em;
        font-weight: bold;
        padding: 0;
        margin: 5px 0;
      }
      .github {
        font-size: 12px;
        color: @primary;
      }
      p {
        margin: 10px 0;
        font-size: 13px;
        color: #888;
        &.technology{
          color: #373737;
          font-weight: 600;
        }
      }
    }
  }
}
</style>