<template>
  <div class="carousel-item-container" ref="wrapper" @mousemove="handleMouseMove" @mouseleave="handleMouseLeave">
    <div class="img" :style="imgPosition" ref="inner">
      <ImageLoader :src="bigImg" :placeholder="midImg" @load="handleLoad" />
    </div>
    <div class="title" ref="title">{{title}}</div>
    <div class="desc" ref="desc">{{description}}</div>
  </div>
</template>

<script>
import ImageLoader from "@/components/ImageLoader";
export default {
  props: ["bigImg", "midImg", "description", "title"],
  components: {
    ImageLoader,
  },
  methods: {
    handleLoad() {
      this.$refs.title.style.transform = "translateY(-20px) scaleX(1)";
      this.$refs.desc.style.transform = "translateY(20px) scaleX(1)";
    },
    handleMouseMove(e) {
      const rect = this.$refs.wrapper.getBoundingClientRect();
      this.mouseX = e.clientX - rect.x;
      this.mouseY = e.clientY - rect.y;
    },
    handleMouseLeave(){
      this.mouseX = this.center.x;
      this.mouseY = this.center.y;
    },
    init() {
      this.wrapperSize.width = this.$refs.wrapper.clientWidth;
      this.wrapperSize.height = this.$refs.wrapper.clientHeight;
      this.innerSize.width = this.$refs.inner.clientWidth;
      this.innerSize.height = this.$refs.inner.clientHeight;
      this.wrapperLeft = this.$refs.wrapper.getBoundingClientRect().x;
      this.wrapperTop = this.$refs.wrapper.getBoundingClientRect().y;
      this.disWidth = this.innerSize.width - this.wrapperSize.width;
      this.disHeight = this.innerSize.height - this.wrapperSize.height;
    },
  },
  data() {
    return {
      wrapperSize: {}, // 容器尺寸
      innerSize: {}, // 图片尺寸
      mouseX: 0, // 鼠标横坐标
      mouseY: 0, // 鼠标纵坐标
      wrapperLeft: 0, // 容器left值
      wrapperTop: 0, // 容器top值
      disWidth: 0, // 容器与内容相差的宽度
      disHeight: 0, // 容器与内容相差的高度
    };
  },
  computed: {
    imgPosition() {
      // 图片位置
      const left =
        (-this.mouseX / this.wrapperSize.width) * this.disWidth + "px";
      const top =
        (-this.mouseY / this.wrapperSize.height) * this.disHeight + "px";
      return {
        transform : `translate(${left},${top})`,
      };
    },
    center(){
      return {
        x : this.wrapperSize.width / 2,
        y : this.wrapperSize.height / 2
      }
    }
  },
  mounted() {
    this.init();
    this.mouseX = this.center.x;
    this.mouseY = this.center.y;
    window.addEventListener("resize",this.init);
  },
  destroyed(){
    window.removeEventListener("resize",this.init);
  }
};
</script>

<style lang="less" scoped>
@import "~@/styles/var.less";
.carousel-item-container {
  position: relative;
  width: 100%;
  height: 100%;
  color: #fff;
  overflow: hidden;
  .img {
    position: absolute;
    width: 110%;
    height: 110%;
    z-index: -1;
    transition: 0.5s;
  }
  .desc,
  .title {
    left: 30px;
    z-index: 5;
    position: absolute;
    text-shadow: 1px 0 0 rgba(0, 0, 0, 0.5), -1px 0 0 rgba(0, 0, 0, 0.5),
      0 1px 0 rgba(0, 0, 0, 0.5), 0 -1px 0 rgba(0, 0, 0, 0.5);
    transform-origin: left;
    transition: 1s;
  }
  .title {
    transform: translateY(-20px) scaleX(0);
    font-size: 2em;
    top: calc(50% - 20px);
  }
  .desc {
    transform: translateY(20px) scaleX(0);
    font-size: 1.2em;
    color: lighten(@gray, 10%);
    top: calc(50% + 20px);
  }
}
</style>