<template>
  <div class="blog-category-container" v-loading="isLoading">
    <h2>文章分类</h2>
    <TreeNav :list="list" @select="handleSelect" />
  </div>
</template>

<script>
import TreeNav from "../TreeNav";
import fetchData from "@/mixins/fetchData";
import { getBlogTypes } from "@/api/blog.js";
export default {
  mixins: [fetchData([])],
  components: {
    TreeNav,
  },
  computed: {
    categoryId() {
      return +this.$route.params.categoryId || -1;
    },
    limit(){
      return +this.$route.query.limit || 10;
    },
    list() {
      const totalArticleCount = this.data.reduce((a,b)=>a+b.articleCount,0)
      const result = [{name:"全部",id:-1,articleCount :totalArticleCount},
      ...this.data]
      return result.map(it=>({
        ...it,
        isSelected : it.id === this.categoryId,
        articleCount : it.articleCount + "篇"
      }));
    },
  },
  methods: {
    async fetchData() {
      return await getBlogTypes();
    },
    handleSelect(item){
      const query = {
        page : 1,
        limit : this.limit
      };
      if(item.id === -1){
        // 没有分类 跳转到全部
        this.$router.push({
          name : "Blog",
          query,
        })
      }else{
        this.$router.push({
          name : "CategoryBlog",
          query,
          params : {
            categoryId : item.id
          }
        })
      }

    }
  },
};
</script>

<style scoped lang="less">
.blog-category-container {
  width: 300px;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
  height: 100%;
  h2 {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 1em;
    margin: 0 20px;
  }
}
</style>