<template>
  <ul class="tree-nav-container">
    <li v-for="(item,i) in list" :key="i">
      <span :class="{active:item.isSelected}" @click="handleClick(item)">{{ item.name }}</span><span class="articleCount">{{ item.articleCount }}</span>
      <TreeNav :list="item.children" />
    </li>
  </ul>
</template>

<script>
export default {
  name: "TreeNav",
  props: {
    list: {
      type: Array,
      default: () => [],
    },
  },
  methods : {
    handleClick(item){
      this.$emit("select", item);
    }
  }
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.tree-nav-container {
  list-style: none;
  padding: 0;
  li {
    margin-left: 20px;
    min-height: 40px;
    line-height: 40px;
    cursor: pointer;
    font-size: 14px;
    .articleCount{
      font-size: 12px;
      margin-left: 1em;
      color: #888;
    }
  }
  .active {
    font-weight: bold;
    color: @warn;
  }
}
</style>