<template>
    <div class="about-container" v-loading="loading && !loaded">
        <iframe v-if="src" :src="src" class="main" frameborder="0" @load="loaded = true"></iframe>        
    </div>
</template>

<script>
import {mapState} from 'vuex';
export default {
    data() {
        return {
            loaded : false,
        }
    },
    computed : mapState('about',{
        src : 'data',
        loading:'loading',
    }),
    created() {
        this.$store.dispatch("about/fetchAbout");
    },
}
</script>

<style scoped lang="less">
    .about-container{
        width: 100%;
        height: 100%;
        position: relative;
        .main{
            width: 100%;
            height: 100%;
        }
    }
</style>