import axios from "axios";
import showMessage from "../util/showMessage.js";
const axs = axios.create({});
// axios 拦截器
axs.interceptors.response.use(function(res){
    if(res.data.code != 0){
        showMessage({
            content : res.data.msg,
            type : "error",
        })
        return null;
    }else {
        return res.data.data;
    }    
})
export default axs;